export default class extends React.Component {
    render() {
        const { products } = this.props

        return (
            <ul className="products">
                {
                    products.map((product) => (
                    <li class="product">
                        {product.name}
                    </li>
                )) }
                
                <style jsx>{`
                    .products {
                        list-style: none;
                    }

                    .product {
                        background-color: red;
                    }
                `}</style>
            </ul>
        )
    }
}